.transaction-history{
    margin-left: auto;
    margin-right: auto;
    background-color: white;
    width: 600px;
    text-align: center;
    border: none;
    color: rgb(122, 120, 120);
    box-shadow: 0 0 10px rgba(0,0,0,0.2); /* Параметры тени */
    margin-bottom: 40px;
    margin-top: 30px;
}

.transaction-history thead{
    background-color: rgb(22, 198, 204);
    color: white;
    text-transform: uppercase;
    font-size: 14px;    
}

.transaction-history th{
    padding-top: 12px;
    padding-bottom: 15px;
    font-weight: 500;
}

.transaction-history tbody tr{
    height: 50px;
}

.transaction-history tbody tr:nth-child(2n){
    background-color: rgb(200, 225, 226);
}